/**
 * 设置表格自适应高度
 */
export function setTableH() {
    return window.innerHeight - 160
}

export function setContainerH() {
    return {
        'background-color': 'rgb(238,241,246)',
        'height': (window.innerHeight - 75) + 'px'
    }
}



